'use client';

import React, { forwardRef, ChangeEvent, useState } from 'react';
import { cn } from '@/lib/utils';
import { Upload } from 'lucide-react';

export interface FileInputProps extends React.InputHTMLAttributes<HTMLInputElement> {
  /**
   * Text to display when no file is selected.
   * Defaults to "Choose file(s)"
   */
  label?: string;
}

const FileInput = forwardRef<HTMLInputElement, FileInputProps>(({
  className,
  label = 'Choose files',
  multiple = false,
  accept,
  onChange,
  ...props
}, ref) => {
  const [files, setFiles] = useState<File[]>([]);

  const handleFiles = (e: ChangeEvent<HTMLInputElement>) => {
    const selected = Array.from(e.target.files || []);
    setFiles(selected);
    if (onChange) onChange(e);
  };

  const labelText = files.length === 0
    ? label
    : files.length === 1
      ? files[0].name
      : `${files.length} files selected`;

  return (
    <div>
      <label className="cursor-pointer">
        <input
          type="file"
          ref={ref}
          className="sr-only peer"
          multiple={multiple}
          accept={accept}
          onChange={handleFiles}
          {...props}
        />
        <div
          className={cn(
            'file-input file-input-bordered w-full',
            'flex items-center space-x-2 rounded-md',
            'px-3 py-2 bg-base-100 text-base-content',
            'peer-focus:ring-2 peer-focus:ring-primary',
            className
          )}
        >
          <Upload className="h-5 w-5 text-primary" />
          <span className="truncate">{labelText}</span>
        </div>
      </label>

      {files.length > 1 && (
        <select className="mt-2 select select-bordered w-full">
          {files.map((file, i) => (
            <option key={i} value={i.toString()}>
              {file.name}
            </option>
          ))}
        </select>
      )}
    </div>
  );
});

FileInput.displayName = 'FileInput';

export default FileInput;
